@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500&display=swap");

* {
	box-sizing: border-box;
}

body {
	padding: 0;
	margin: 0;
	height: 100%;
	font-family: "Inter", sans-serif;
	background-color: white;
}

header {
	text-align: center;
	margin: 0 auto;
	font-size: 14px;
	font-weight: 500;
	padding-top: 1rem;
	padding-bottom: 2rem;
}

#appTitle {
	color: #3730a3;
	padding-bottom: 1rem;
}

#appTitle #appTitleContainer {
	margin: 0 auto;
	width: 185px;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
}

#appIntro {
	color: #818cf8;
	padding-left: 0.7em;
	padding-right: 0.7em;
	font-weight: 400;
	line-height: 1.2em;
}

.textDiv {
	margin: 0 auto;
}

#textDivTitle {
	font-size: 14px;
	color: #374151;
}

#regSpan {
	font-weight: 500;
}

#resetSpan {
	float: right;
	font-size: 12px;
	color: #1d4ed8;
	padding-right: 7px;
	cursor: pointer;
}

#textAreaDiv {
	margin: 0 auto;
	border: 1px #d1d5db solid;
	border-radius: 4px;
	padding: 0.2em;
}

#txtArea {
	width: 100%;
	height: 30%;
	font-size: 14px;
	font-weight: 400;
	font-family: "Inter", sans-serif;
	color: #585d68;
	line-height: 1.2em;
	resize: none;
	outline: none;
	border: none;
}

.resultsDiv {
	margin: 0 auto;
}

#resultsDivTitle {
	font-size: 14px;
	color: #374151;
	padding-bottom: 11px;
	padding-top: 11px;
	position: sticky;
	top: 0;
	background-color: white;
	font-weight: 500;
}

#resultsDivTitle span {
	float: right;
	color: #9ca3af;
	font-weight: 400;
	padding-right: 7px;
}

table {
	margin: 0;
	padding: 0;
	border-collapse: collapse;
	border-spacing: 0;
}

#rangesTable {
	width: 100%;
}

#rangesTable th {
	font-size: 16px;
	font-weight: 500;
	color: #5b21b6;
	text-align: left;
	padding: 0.75em;
	background-color: #ede9fe;
	position: sticky;
	top: 39px;
}

#rangesTable th:first-child {
	border-radius: 4px 0 0 4px;
}

#rangesTable th:last-child {
	border-radius: 0 4px 4px 0;
}

#rangesTable td {
	font-size: 14px;
	font-weight: 400;
	color: #6b7280;
	padding: 0.8em;
	border-bottom: 1px solid #dfdfdf;
}

@media only screen and (max-width: 425px) {
	header {
		width: 94%;
		max-width: 353px;
	}

	#textDivTitle {
		margin-bottom: 0.5em;
	}

	.textDiv,
	.resultsDiv {
		width: 94%;
		max-width: 375px;
	}

	.resultsDiv {
		margin-top: 1rem;
		margin-bottom: 4rem;
	}
}

@media only screen and (min-width: 426px) and (max-width: 767px) {
	header {
		width: 94%;
		max-width: 353px;
	}

	#textDivTitle {
		margin-bottom: 0.5em;
	}

	.textDiv,
	.resultsDiv {
		width: 90%;
		max-width: 560px;
	}

	.resultsDiv {
		margin-top: 1rem;
		margin-bottom: 4rem;
	}
}

@media only screen and (min-width: 768px) {
	.outestContainer {
		display: flex;
		flex-direction: column;
		height: 100%;
		background-color: white;
	}

	header {
		padding-top: 1rem;
		padding-bottom: 2rem;
		width: 426px;
	}

	#textDivTitle {
		margin-bottom: 11px;
	}

	.txtResContainer {
		margin: 0 auto;
		display: flex;
		flex: 1;
		/* flex-direction: row;
		justify-content: center; */
		max-width: 1000px;
	}

	.textDiv,
	.resultsDiv {
		width: 50%;
		padding: 2rem;
	}

	/* Control the left side */
	.textDiv {
		padding-right: 0.5rem;
		padding-top: 11px;
	}

	/* Control the right side */
	.resultsDiv {
		padding-left: 0.5rem;
		padding-top: 0;
	}

